*{
    list-style-type: none;
}

ul{
    display: flex;
    flex-direction: row;
    margin: 0;
    padding-left: 0;
    padding-top: 0;
}

li{
    padding: 0.55rem;
    color: white;
    text-align: center;
    text-justify: auto;
    font-size: 1.2rem;
    align-self: center;
    font-weight: lighter;
}

.link{
    padding: 0.55rem;
    color: white;
    text-align: center;
    text-justify: auto;
    font-size: 1.2rem;
    align-self: center;
    font-weight: lighter;

}

li img{
    width: 15rem;
    padding-right: 0.25rem;
}



.Nav{

    padding-top: 5px;
    height: 60px;
    width: 100%;
    top: 0px;
    text-align: center;
    font-family: 'EB Garamond', serif;
    font-weight: 500;
    margin-bottom: 5rem;
    z-index: 4;
}


@media screen and (max-width: 650px){

    ul{
        display: flex;
        flex-direction: column;
    }
    li{
        padding: 0px;
    }
    .Nav{
        padding-bottom: 20px;
    }

}



